import { AppComponent } from './app.component';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule, JsonpModule } from '@angular/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ChartModule } from 'angular-highcharts';
// My libraries
import { Routing } from './app.routes';

/** MY SERVICE **/
import { AuthenticationService } from './services/authentication.service';
import { HttpClientService } from './services/httpClient.service';
import { LoggingService } from './services/logging.service';
import { ShowTooltipService } from './services/helpers/tooltip.helper';

/** MY COMPONENT **/
import { DashboardComponent } from './components/playouts/dashboard/dashboard.component';
import { LoginComponent } from './components/playouts/login/login.component';
import { MenuComponent } from './components/playouts/menu/menu.component';
import { HeaderComponent } from './components/playouts/header/header.component';
import { AccountComponent  } from './components/mains/account/account.component';
import { FooterComponent } from './components/playouts/footer/footer.component';
import { IntroduceComponent } from './components/mains/introduce/introduce.component';
import { CategoryComponent } from './components/mains/category/category.component';
import { ProductComponent } from './components/mains/product/product.component';
import { ReportComponent } from './components/mains/report/report.component';
import { WebsiteComponent } from './components/mains/website/website.component';


@NgModule({
  declarations: [
    AppComponent,
    DashboardComponent,
    LoginComponent,
    MenuComponent,
    HeaderComponent,
    AccountComponent,
    FooterComponent,
    IntroduceComponent,
    CategoryComponent,
    ProductComponent,
    ReportComponent,
    WebsiteComponent
  ],
  imports: [
    BrowserModule,
    Routing,
    HttpModule,
    FormsModule,
    BrowserModule,
    FormsModule,
    HttpModule,
    JsonpModule,
    BrowserAnimationsModule,
    ChartModule

  ],
  providers: [
    AuthenticationService,
    HttpClientService,
    LoggingService,
    ShowTooltipService



  ],
  bootstrap: [AppComponent]
})
export class AppModule {
}



